.RegistrationPage {
  background-color: $violet100;
  height: 100vh;
  position: relative;
}

.registration {

  &__left-box {
    width: 50%;
    background-image: url('./assets/pappi.jpeg');
    background-position: center;
    background-size: cover;
  }

  &__right-box {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
  }
}

.registration__from {
  width: 260px;
  box-shadow: 2px 2px 10px $stone500;
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  padding: 100px 130px;
  background-color: $violet200;
  border-radius: 10px;

  &--title {
    display: flex;
    margin: 0 auto;
  }

  &--subtitle {
    color: $stone500;
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 15px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  row-gap: 20px;

  &__input {
    border: 1px solid $violet400;
    border-radius: 8px;
    padding: 10px;
    outline: none;
  }

  .label {
    position: relative;
    display: flex;
    flex-direction: column;

    .error--message {
      display: flex;
      column-gap: 4px;
      font-weight: 400;
      font-size: 12px;
      position: absolute;
      top: 38px;
      left: 5px;
      color: $error-500;
    }

    .error--message:before {
      background: url("./assets/error-icon.svg");
      display: flex;
      content: "";
      width: 16px;
      height: 16px;

    }
  }



  &__button {
    background-color: $violet700;
    border: 1px solid $violet700;
    padding: 10px;
    color: #FFFFFF;
    border-radius: 8px;
    margin-top: 10px;
    cursor: pointer;
    box-shadow: 2px 2px 2px $stone600;
    transition: all 400ms ease;
  }

  &__button:hover {
    box-shadow: none;
  }

  &__button:active {
    background-color: $violet600;
  }
}

.button__close {
  position: absolute;
  padding: 0;
  margin: 0;
  top: 20px;
  right: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.button__close:active {
  opacity: 0.7;
}

.isDangerous {
  border-color: $error-500;
}

.user-menu {
  display: flex;
  gap: 16px;

  &__user {
    cursor: pointer;
  }

  &__logout {
    cursor: pointer;
  }
}

.user-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  border: 1px solid $stone50;
  background-color: $violet700;
  height: 42px;
  padding: 0 18px;
  cursor: pointer;
}
